    }
    .cards:nth-of-type(6) {
        background: url(../../img/39-1068x712.jpg) center/cover;
    }
    .cards:nth-of-type(7) {
        background: url(../../img/29-1068x712.jpg) center/cover;
    }
    .cards:nth-of-type(8) {
